import { useFormState } from 'react-final-form'
import { defineMessages, useIntl } from 'react-intl'
import { CopyEmbedSourceCode } from 'ui-components/CopyEmbedSourceCode'
import { CtaButtonContainer } from './styled-components'
import { FormValues } from './types'
import { getCallToActionButtonCode } from './util'

const messages = defineMessages({
  irpCallToActionSourceCodeEmbedLabel: {
    defaultMessage: 'Source code to embed Call to Action button of IRP',
    description: 'IRP call to action source code embed label',
  },
  irpCallToActionSourceCodeEmbedButtonLabel: {
    defaultMessage: 'Copy source code to Call to Action button of the IRP',
    description: 'IRP call to action source code copy button label',
  },
  requestMeetingLabel: {
    defaultMessage: 'Request Meeting',
    description: 'CTA button default label',
  },
})

export const IrpCtaEmbedSourceCodeCopy = () => {
  const { formatMessage } = useIntl()
  const {
    values: { external_widget_token, ext_widget_theme },
    initialValues: { enable_external_widget: enableExternalWidget },
  } = useFormState<FormValues>()

  const ctaEmbedCode = getCallToActionButtonCode(
    external_widget_token,
    JSON.parse(ext_widget_theme).colors.primary,
    formatMessage(messages.requestMeetingLabel)
  )

  return (
    <>
      <CtaButtonContainer dangerouslySetInnerHTML={{ __html: ctaEmbedCode }} />
      <CopyEmbedSourceCode
        title={formatMessage(messages.irpCallToActionSourceCodeEmbedLabel)}
        buttonLabel={formatMessage(
          messages.irpCallToActionSourceCodeEmbedButtonLabel
        )}
        disabled={enableExternalWidget === 'false'}
        link={ctaEmbedCode}
      />
    </>
  )
}
