import { ReactNode, VFC } from 'react'
import { FormattedMessage } from 'react-intl'
import { Field } from 'ui-components/Field'
import { Link, ViewPageLabel, WidgetRoot } from './styled-components'
import { IrpViewPageLinkProps } from './types'

export const IrpViewPageLink: VFC<IrpViewPageLinkProps> = ({
  name,
  label,
  disabled,
}) => (
  <WidgetRoot>
    <ViewPageLabel>{label}</ViewPageLabel>
    <Field<string>
      name={name}
      render={({ input }) => (
        <FormattedMessage
          defaultMessage="<a>View Page</a>"
          description="Link label"
          values={{
            a: (chunks: ReactNode) => (
              <Link
                href={input.value}
                rel="noopener noreferrer"
                target="_blank"
                $disabled={disabled}
              >
                {chunks}
              </Link>
            ),
          }}
        />
      )}
    />
  </WidgetRoot>
)
