import { useStyletron } from '@jifflenow/brightui'

export const useIrpTheme = () => {
  const { colors } = useStyletron()[1]
  const themes = {
    [colors.primary]: {
      primary: colors.primary,
      primaryDark: colors.primary500,
      primaryLight: colors.primary50,
      shadowFocus: colors.primary50,
    },
    [colors.mono900]: {
      primary: colors.mono900,
      primaryDark: colors.mono700,
      primaryLight: colors.mono400,
      shadowFocus: 'rgba(0, 0, 0, 0.5)',
    },
    '#535353': {
      primary: '#535353',
      primaryDark: '#929292',
      primaryLight: colors.mono400,
      shadowFocus: colors.mono400,
    },
  }

  return themes
}
