import { VFC, useEffect, useMemo, useRef, useState } from 'react'
import { Config, FORM_ERROR } from 'final-form'
import { Form as FinalForm } from 'react-final-form'
import { Prompt } from 'react-router-dom'
import { defineMessages, FormattedMessage, useIntl } from 'react-intl'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { Option } from '@jifflenow/brightui/select'
import { useMe } from 'providers/MeProvider'
import { EventMainHeader } from 'components/EventMainHeader'
import {
  mutateBulkUpsertEventConfigs,
  useApolloClient,
  useEventConfigQuery,
} from 'gql'
import {
  bulkFetchLocationConfigsByName_bulkFetchLocationConfigsByName as LocationConfigsByName,
  bulkFetchLocationConfigsByName_bulkFetchLocationConfigsByName_configValue_CustomNameValue as CustomNameValue,
  bulkFetchLocationConfigsByName_bulkFetchLocationConfigsByName_configValue_IrpEnabledEngagementTypes as IrpEnabledEngagementTypes,
} from 'gql/types/bulkFetchLocationConfigsByName'
import { BulkLocationConfigUpdateInput } from 'gql/types/globalTypes'
import {
  bulkUpsertLocationConfigsVariables,
  bulkUpsertLocationConfigs_bulkUpsertLocationConfigs_locationConfigs_configValue_CustomNameValue as UpsertCustomNameValue,
  bulkUpsertLocationConfigs_bulkUpsertLocationConfigs_locationConfigs_configValue_IrpEnabledEngagementTypes as UpsertIrpEnabledEngagementTypes,
} from 'gql/types/bulkUpsertLocationConfigs'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { useEventLocation } from 'providers/LocationProvider'
import { upsertSystemConfigurations, useQueryClient } from 'rest'
import { components } from 'types/openapi'
import { Container, Heading, StyledForm } from './styled-components'
import { FormFields } from './FormFields'
import { FormProps, FormValues } from './types'
import { IrpContentLoader } from './IrpContentLoader'
import { Footer } from './Footer'

type LocationConfigFormPayload = components['schemas']['LocationConfigForm']['configs']

const messages = defineMessages({
  promptMessage: {
    defaultMessage: 'Are you sure you want to leave?',
    description: 'Prompt message',
  },
  updateError: {
    defaultMessage:
      '{failureCount, number} out of {totalCount, number} {totalCount, plural, one {config} other {configs}} failed to save',
    description: 'Error message',
  },
  genericFieldErrorMessage: {
    defaultMessage: 'Error saving changes',
    description: 'Field error message',
  },
  successMessage: {
    defaultMessage: 'Your changes have been saved successfully',
    description: 'Success message',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
})

const LoadingShimmer = () => (
  <Container>
    <IrpContentLoader />
  </Container>
)

const Form: VFC<FormProps> = ({
  loading,
  formRenderProps: { handleSubmit, dirty },
}) => {
  const { formatMessage } = useIntl()
  const shimmerShown = useRef(false)
  const unload = useBeforeUnload()

  useEffect(() => {
    unload(dirty)
  }, [dirty, unload])

  useEffect(() => {
    if (!loading && !shimmerShown.current) {
      shimmerShown.current = true
    }
  }, [loading])

  return (
    <StyledForm onSubmit={handleSubmit} noValidate>
      <Prompt when={dirty} message={formatMessage(messages.promptMessage)} />
      {loading && !shimmerShown.current ? (
        <LoadingShimmer />
      ) : (
        <>
          <Container>
            <Heading>
              <FormattedMessage
                defaultMessage="Inbound Request Page"
                description="Page heading"
              />
            </Heading>
            <FormFields />
          </Container>
          <Footer />
        </>
      )}
    </StyledForm>
  )
}

const filterLocationConfigs = (
  response: LocationConfigsByName[],
  fields: string[]
) =>
  response.filter(({ configName }) => !!fields.find(key => key === configName))

const commonSettingNames = [
  'enable_external_widget',
  'external_widget_token',
  'enable_company_logo_in_ext_widget',
  'enable_event_banner_in_ext_widget',
  'external_widget_header_text',
  'external_widget_help_text',
  'external_widget_form_header_text',
  'ext_widget_theme',
]
const engagementSettingName = ['irp_enabled_engagement_types']
const meetNowSettingNames = [
  'enable_irp_instant_meeting',
  'irp_instant_meeting_url',
  'irp_instant_meeting_label',
]

export const InboundRequestPage = () => {
  const { formatMessage } = useIntl()
  const client = useApolloClient()
  const queryClient = useQueryClient()
  const { role, eventRole } = useMe().data.attributes
  const [showLoader, setShowLoader] = useState(false)

  const {
    attributes: { systemName: eventName, version },
  } = useEventLocation().location!

  const settingNames = useMemo(
    () => [
      ...commonSettingNames,
      ...((eventRole ?? role) === 'CSM' ? meetNowSettingNames : []),
    ],
    [eventRole, role]
  )

  const {
    loading,
    bulkFetchLocationConfigsByName = [],
    refetch,
  } = useEventConfigQuery({
    notifyOnNetworkStatusChange: true,
    variables: {
      names:
        version === '1'
          ? settingNames
          : [...settingNames, ...engagementSettingName],
    },
    fetchPolicy: 'cache-and-network',
    onError: () => {
      showErrorToast(formatMessage(messages.genericError))
    },
  })

  const settings = useMemo(
    () => filterLocationConfigs(bulkFetchLocationConfigsByName, settingNames),
    [bulkFetchLocationConfigsByName, settingNames]
  )

  const engagementSetting = useMemo(
    () =>
      filterLocationConfigs(
        bulkFetchLocationConfigsByName,
        engagementSettingName
      ),
    [bulkFetchLocationConfigsByName]
  )

  const initialValues: FormValues = useMemo(
    () => ({
      ...settings.reduce(
        (acc, { configName, configValue }) => ({
          ...acc,
          [configName]: (configValue as CustomNameValue).value,
        }),
        {} as FormValues
      ),
      ...(version === '2' &&
        engagementSetting.reduce(
          (acc, { configName, configValue }) => ({
            ...acc,
            [configName]: JSON.stringify(
              (configValue as IrpEnabledEngagementTypes).irpEnabledEngagementTypes.map(
                ({ __typename, ...rest }) => rest
              )
            ),
          }),
          {} as FormValues
        )),
    }),
    [engagementSetting, settings, version]
  )

  const handleSubmit: Config<FormValues>['onSubmit'] = async (
    values,
    { change, getFieldState }
  ) => {
    try {
      let configsToUpdate = settings.reduce<BulkLocationConfigUpdateInput[]>(
        (acc, { configName, configValue: previousValue }) => {
          const { display_name, value } = previousValue as CustomNameValue

          const data: BulkLocationConfigUpdateInput[] =
            value !== values[configName as keyof FormValues]
              ? [
                  ...acc,
                  {
                    configName,
                    configValue: {
                      display_name,
                      value: values[configName as keyof FormValues],
                    },
                  },
                ]
              : acc

          return data
        },
        []
      )

      setShowLoader(true)

      if (version === '1') {
        const configs: LocationConfigFormPayload = configsToUpdate.reduce(
          (acc, { configName, configValue }) => ({
            ...acc,
            [configName]:
              configName === 'ext_widget_theme'
                ? JSON.parse(values[configName as keyof FormValues])
                : configValue?.value,
          }),
          {}
        )
        const engagementTypesField = getFieldState(
          'irp_enabled_engagement_types'
        )
        if (engagementTypesField?.dirty) {
          configs.irp_enabled_engagement_types = JSON.parse(
            values['irp_enabled_engagement_types' as keyof FormValues]
          ).map(({ id }: Option) => id)
        }

        const { failureCount, totalCount, locationConfigs, successCount } = (
          await upsertSystemConfigurations({
            path: { eventName },
            payload: { configs },
          })
        ).data

        setShowLoader(false)
        if (failureCount) {
          showErrorToast(
            formatMessage(messages.updateError, { totalCount, failureCount })
          )
          locationConfigs.forEach(
            ({ success, configName }) =>
              !success &&
              change(
                configName as keyof FormValues,
                initialValues[configName as keyof FormValues]
              )
          )
          const errors = locationConfigs.reduce(
            (acc, { success, configName }) =>
              success
                ? acc
                : {
                    ...acc,
                    [configName]: formatMessage(
                      messages.genericFieldErrorMessage
                    ),
                  },
            {}
          )
          if (successCount) {
            refetch()
          }

          return errors
        }

        if (engagementTypesField?.dirty) {
          await queryClient.refetchQueries('getEngagementTypeSettings')
        }

        await client.refetchQueries({
          include: ['bulkFetchLocationConfigsByName'],
        })
        client.refetchQueries({ include: ['getEventLocationInfo'] })
      } else if (version === '2') {
        configsToUpdate = engagementSetting.reduce<
          BulkLocationConfigUpdateInput[]
        >((acc, { configName, configValue: previousValue }) => {
          const {
            display_name,
            irpEnabledEngagementTypes,
          } = previousValue as IrpEnabledEngagementTypes

          const parsedValue = JSON.parse(values[configName as keyof FormValues])

          return JSON.stringify(
            irpEnabledEngagementTypes.map(({ __typename, ...rest }) => rest)
          ) !== values[configName as keyof FormValues]
            ? [
                ...acc,
                {
                  configName,
                  irpEnabledEngagementTypes: {
                    display_name,
                    value: parsedValue.some(({ id }: Option) => id === 'ALL')
                      ? [
                          {
                            display_name: 'All',
                            id: 'ALL',
                          },
                        ]
                      : parsedValue,
                  },
                },
              ]
            : acc
        }, configsToUpdate)

        const variables: bulkUpsertLocationConfigsVariables = {
          configsToUpdate,
        }

        const { failureCount, totalCount, locationConfigs, successCount } = (
          await mutateBulkUpsertEventConfigs(client, {
            variables,
            refetchQueries: ['bulkFetchLocationConfigsByName'],
          })
        ).data!.bulkUpsertLocationConfigs

        setShowLoader(false)
        if (failureCount) {
          showErrorToast(
            formatMessage(messages.updateError, { totalCount, failureCount })
          )
          locationConfigs.forEach(
            ({ success, configName, configValue }) =>
              !success &&
              change(
                configName as keyof FormValues,
                configValue.__typename === 'CustomNameValue'
                  ? (configValue as UpsertCustomNameValue).value
                  : JSON.stringify(
                      (configValue as UpsertIrpEnabledEngagementTypes).irpEnabledEngagementTypes.map(
                        ({ __typename, ...rest }) => rest
                      )
                    )
              )
          )
          const errors = locationConfigs.reduce(
            (acc, { success, configName }) =>
              success
                ? acc
                : {
                    ...acc,
                    [configName]: formatMessage(
                      messages.genericFieldErrorMessage
                    ),
                  },
            {}
          )
          if (successCount) {
            refetch()
          }

          return errors
        }

        refetch()
        client.refetchQueries({ include: ['getEventLocationInfo'] })
      }
      showSuccessToast(formatMessage(messages.successMessage))
      return undefined
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
      return { [FORM_ERROR]: { code: 'GenericError' } }
    }
  }

  return (
    <>
      {showLoader && <ModalSpinner />}
      <EventMainHeader />
      <FinalForm<FormValues>
        onSubmit={handleSubmit}
        initialValues={initialValues}
        render={props => <Form formRenderProps={props} loading={loading} />}
      />
    </>
  )
}
