import { useFormState } from 'react-final-form'
import { defineMessages, useIntl } from 'react-intl'
import { CopyEmbedSourceCode } from 'ui-components/CopyEmbedSourceCode'
import { CtaButtonContainer } from './styled-components'
import { FormValues } from './types'
import { getCallToActionButtonCode } from './util'

const messages = defineMessages({
  meetNowCTAEmbedLabel: {
    defaultMessage: 'Source code to embed Call to Action button of Meet Now',
    description: 'IRP call to action source code embed label',
  },
  meetNowCTACopyButtonLabel: {
    defaultMessage:
      'Copy source code to Call to Action button of the Meet Now',
    description: 'IRP call to action source code copy button label',
  },
  requestMeetingLabel: {
    defaultMessage: 'Request Meeting',
    description: 'CTA button default label',
  },
})

export const MeetNowCtaEmbedSourceCodeCopy = () => {
  const { formatMessage } = useIntl()
  const {
    values: {
      irp_instant_meeting_url,
      irp_instant_meeting_label,
      ext_widget_theme,
    },
    initialValues: { enable_irp_instant_meeting: enableIrpInstantMeeting },
  } = useFormState<FormValues>()

  const ctaEmbedCode = getCallToActionButtonCode(
    irp_instant_meeting_url,
    JSON.parse(ext_widget_theme).colors.primary,
    irp_instant_meeting_label || formatMessage(messages.requestMeetingLabel)
  )

  return (
    <>
      <CtaButtonContainer dangerouslySetInnerHTML={{ __html: ctaEmbedCode }} />
      <CopyEmbedSourceCode
        title={formatMessage(messages.meetNowCTAEmbedLabel)}
        buttonLabel={formatMessage(messages.meetNowCTACopyButtonLabel)}
        disabled={enableIrpInstantMeeting === 'false'}
        link={ctaEmbedCode}
      />
    </>
  )
}
