import { VFC } from 'react'
import { Field, Validation } from 'ui-components/Field'
import { Input } from '@jifflenow/brightui/input'
import { FormControlStyled } from './styled-components'
import { IrpInputProps } from './types'

export const IrpInput: VFC<IrpInputProps> = ({
  label,
  name,
  placeholder,
  required = false,
}) => (
  <Field<string>
    name={name}
    parse={value => value}
    validations={required ? [Validation.REQUIRED] : undefined}
    render={({ input, meta: { error, submitError } }) => (
      <FormControlStyled label={label} error={submitError || error}>
        <Input {...input} type="text" placeholder={placeholder} />
      </FormControlStyled>
    )}
  />
)
