import { useMemo } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useForm } from 'react-final-form'
import { Option, Select, Value } from '@jifflenow/brightui/select'
import { Field } from 'ui-components/Field'
import { useEventLocation } from 'providers/LocationProvider'
import { useEngagementTypesQuery } from 'queries'
import {
  EngagementTypesFormControl,
  IrpEngagementTypeHelpText,
  IrpEngagementTypeRoot,
} from './styled-components'
import { FormValues } from './types'

const messages = defineMessages({
  allLabel: {
    defaultMessage: 'All',
    description: 'Option label',
  },
  roleLabel: {
    defaultMessage: 'Enable IRP for below engagement types',
    description: 'Field label',
  },
  searchPlaceholder: {
    defaultMessage: 'Search by engagement type',
    description: 'Search placeholder',
  },
  helpText: {
    defaultMessage:
      'Select All to enable IRP for all current and future created engagement types',
    description: 'Help text',
  },
})

export const IrpEngagementTypes = () => {
  const { formatMessage } = useIntl()
  const { initialize } = useForm()

  const {
    systemName: eventName,
    version,
  } = useEventLocation().location!.attributes

  const { isLoading, data: fetchEngagementTypes } = useEngagementTypesQuery({
    version,
    eventName,
    showIrpEnabled: true,
    showOnlyNonConsecutiveEngagementTypes: true,
  })

  const options = useMemo(
    () => [
      {
        display_name: formatMessage(messages.allLabel),
        id: 'ALL',
      },
      ...(fetchEngagementTypes
        ?.slice()
        .sort((a, b) => a.displayOrder - b.displayOrder)
        .map(({ id, name }) => ({
          display_name: name,
          id,
        })) ?? []),
    ],
    [fetchEngagementTypes, formatMessage]
  )

  const initialValue = fetchEngagementTypes
    ?.filter(({ irp_enabled }) => irp_enabled)
    .map(({ name, id }) => ({ display_name: name, id }))

  const fieldProps =
    version === '1'
      ? {
          initialValue: JSON.stringify(initialValue),
          afterSubmit() {
            initialize((values: FormValues) => ({
              ...values,
              irp_enabled_engagement_types: JSON.stringify(initialValue),
            }))
          },
        }
      : {}

  return (
    <Field<string>
      name="irp_enabled_engagement_types"
      format={(value: string) => (value ? JSON.parse(value) : [])}
      parse={(value: Option[]) => JSON.stringify(value)}
      {...fieldProps}
      render={({ input, meta: { submitError } }) => (
        <IrpEngagementTypeRoot>
          <EngagementTypesFormControl
            label={formatMessage(messages.roleLabel)}
            error={submitError}
          >
            <Select
              {...input}
              multi
              id="irp_enabled_engagement_types"
              isLoading={isLoading}
              labelKey="display_name"
              options={options}
              backspaceClearsInputValue
              searchable
              placeholder={formatMessage(messages.searchPlaceholder)}
              onChange={({ value }: { value: Value[] }) =>
                input.onChange(value)
              }
            />
          </EngagementTypesFormControl>
          <IrpEngagementTypeHelpText>
            {formatMessage(messages.helpText)}
          </IrpEngagementTypeHelpText>
        </IrpEngagementTypeRoot>
      )}
    />
  )
}
