import { RadioOverrides } from '@jifflenow/brightui/radio'
import { CheckboxOverrides } from '@jifflenow/brightui/checkbox'

export const toggleOverride: CheckboxOverrides = {
  Root: {
    style: ({ $theme: { sizing } }) => ({
      marginLeft: sizing.scale400,
    }),
  },
}

export const getColorThemeRadioOverride = (
  color: string,
  ariaLabel: string
): RadioOverrides => ({
  Input: {
    props: { 'aria-label': ariaLabel },
  },
  RadioMarkOuter: {
    style: ({ $theme: { sizing }, $checked }) => ({
      height: sizing.scale800,
      width: sizing.scale800,
      borderTopLeftRadius: sizing.scale100,
      borderTopRightRadius: sizing.scale100,
      borderBottomRightRadius: sizing.scale100,
      borderBottomLeftRadius: sizing.scale100,
      borderColor: color,
      ...($checked ? null : { backgroundColor: color }),
    }),
  },
  RadioMarkInner: {
    style: ({ $theme: { sizing, colors }, $checked }) => ({
      height: sizing.scale700,
      width: sizing.scale700,
      borderTopLeftRadius: sizing.scale100,
      borderTopRightRadius: sizing.scale100,
      borderBottomRightRadius: sizing.scale100,
      borderBottomLeftRadius: sizing.scale100,
      backgroundColor: color,
      ...($checked
        ? {
            borderStyle: 'solid',
            borderWidth: '1px',
            borderColor: colors.white,
          }
        : null),
    }),
  },
  Label: {
    style: {
      paddingLeft: 0,
    },
  },
  Root: {
    style: ({ $theme: { sizing } }) => ({
      marginLeft: sizing.scale600,
      marginRight: 0,
      marginTop: 0,
      marginBottom: 0,
    }),
  },
})
