import { useFormState } from 'react-final-form'
import { defineMessages, useIntl } from 'react-intl'
import { CopyEmbedSourceCode } from 'ui-components/CopyEmbedSourceCode'
import { Field } from 'ui-components/Field'
import { FormValues } from './types'
import { getIframeCode } from './util'

const messages = defineMessages({
  meetNowSourceCodeEmbedLabel: {
    defaultMessage: 'Source code to embed the Meet Now',
    description: 'Meet now source code embed label',
  },
  meetNowSourceCodeEmbedButtonLabel: {
    defaultMessage: 'Copy iframe code to embed the Meet Now',
    description: 'Meet now source code copy button label',
  },
})

export const MeetNowEmbedSourceCodeCopy = () => {
  const { formatMessage } = useIntl()
  const {
    enable_irp_instant_meeting: enableIrpInstantMeeting,
  } = useFormState<FormValues>().initialValues

  return (
    <Field<string>
      name="irp_instant_meeting_url"
      render={({ input: { value } }) => (
        <CopyEmbedSourceCode
          title={formatMessage(messages.meetNowSourceCodeEmbedLabel)}
          buttonLabel={formatMessage(
            messages.meetNowSourceCodeEmbedButtonLabel
          )}
          disabled={enableIrpInstantMeeting === 'false'}
          link={getIframeCode(value)}
        />
      )}
    />
  )
}
