import { ChangeEvent, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Field } from 'react-final-form'
import { Toggle as BaseToggle } from '@jifflenow/brightui/toggle'
import {
  Error,
  LabelContainer,
  ToggleDesc,
  ToggleLabel,
  WidgetRoot,
} from './styled-components'
import { toggleOverride } from './overrides'

const messages = defineMessages({
  yesLabel: {
    defaultMessage: 'Yes',
    description: 'Toggle label',
  },
  noLabel: {
    defaultMessage: 'No',
    description: 'Toggle label',
  },
})

type ToggleProps = {
  label: string
  name: string
  desc?: string
  disabled?: boolean
}

export const Toggle: VFC<ToggleProps> = ({ name, label, desc }) => {
  const { formatMessage } = useIntl()

  return (
    // TODO: Update the type of the field once the API is available
    <Field<string>
      name={name}
      render={({
        input: { value, onChange },
        meta: { error, submitError },
      }) => (
        <>
          {(submitError || error) && <Error>{submitError || error}</Error>}
          <WidgetRoot>
            <LabelContainer>
              <ToggleLabel $bold={!!desc}>{label}</ToggleLabel>
              <ToggleDesc>{desc}</ToggleDesc>
            </LabelContainer>
            <BaseToggle
              name={name}
              ariaLabel={label}
              checked={value === 'true'}
              onChange={(e: ChangeEvent<HTMLInputElement>) =>
                onChange(e.target.checked ? 'true' : 'false')
              }
              label={formatMessage(
                messages[value === 'true' ? 'yesLabel' : 'noLabel']
              )}
              overrides={toggleOverride}
            />
          </WidgetRoot>
        </>
      )}
    />
  )
}
