import { useFormState } from 'react-final-form'
import { defineMessages, useIntl } from 'react-intl'
import { CopyEmbedSourceCode } from 'ui-components/CopyEmbedSourceCode'
import { Field } from 'ui-components/Field'
import { FormValues } from './types'
import { getIframeCode } from './util'

const messages = defineMessages({
  irpSourceCodeEmbedLabel: {
    defaultMessage: 'Source code to embed the inbound request page',
    description: 'IRP source code embed label',
  },
  irpSourceCodeEmbedButtonLabel: {
    defaultMessage: 'Copy iframe code to embed the IRP',
    description: 'IRP source code copy button label',
  },
})

export const IrpEmbedSourceCodeCopy = () => {
  const { formatMessage } = useIntl()
  const {
    enable_external_widget: enableExternalWidget,
  } = useFormState<FormValues>().initialValues

  return (
    <Field<string>
      name="external_widget_token"
      render={({ input: { value } }) => (
        <CopyEmbedSourceCode
          title={formatMessage(messages.irpSourceCodeEmbedLabel)}
          buttonLabel={formatMessage(messages.irpSourceCodeEmbedButtonLabel)}
          disabled={enableExternalWidget === 'false'}
          link={getIframeCode(value)}
        />
      )}
    />
  )
}
