import { ReactNode } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useFormState } from 'react-final-form'
import { useMe } from 'providers/MeProvider'
import { useEventLocation } from 'providers/LocationProvider'
import { IrpColorTheme } from './IrpColorTheme'
import { IrpViewPageLink } from './IrpViewPageLink'
import {
  CopySourceCodeContainer,
  Divider,
  EditorContainer,
  Group,
  IrpPreview,
  IrpTextEditor,
  NumberCircle,
  PreviewContainer,
} from './styled-components'
import { Toggle } from './Toggle'
import PreviewImage from './preview.png'
import { IrpEditor } from './IrpEditor'
import { IrpEmbedSourceCodeCopy } from './IrpEmbedSourceCodeCopy'
import { IrpCtaEmbedSourceCodeCopy } from './IrpCtaEmbedSourceCodeCopy'
import { IrpEngagementTypes } from './IrpEngagementTypes'
import { IrpInput } from './IrpInput'
import { MeetNowEmbedSourceCodeCopy } from './MeetNowEmbedSourceCodeCopy'
import { MeetNowCtaEmbedSourceCodeCopy } from './MeetNowCtaEmbedSourceCodeCopy'

const messages = defineMessages({
  enableIrpLabel: {
    defaultMessage: 'Enable Inbound Request Page Widget',
    description: 'Enable IRP label',
  },
  enableIrpDesc: {
    defaultMessage: 'Enable external widget for this instance',
    description: 'Enable IRP desc',
  },
  viewIrpPageLabel: {
    defaultMessage: 'Customize Inbound Request Page',
    description: 'IRP view page label text',
  },
  companyLogoLabel: {
    defaultMessage: 'Display company logo?',
    description: 'Company logo label',
  },
  displayEventBannerLabel: {
    defaultMessage: 'Display event banner on the page?',
    description: 'Company logo label',
  },
  enableMeetNowLabel: {
    defaultMessage: 'Enable Meet Now',
    description: 'Enable meet now label',
  },
  viewMeetNowPageLabel: {
    defaultMessage: 'Customize Meet Now',
    description: 'Meet now view page label text',
  },
  meetNowCallToActionInputLabel: {
    defaultMessage: 'Meet Now call to action button text',
    description: 'Meet now call to action input label',
  },
  meetNowCallToActionInputPlaceholder: {
    defaultMessage: 'Please enter text',
    description: 'Meet now call to action input placeholder',
  },
  altPreviewImage: {
    defaultMessage: 'IRP preview',
    description: 'Alt-text',
  },
  irpHeaderLabel: {
    defaultMessage: 'Inbound Request Page Header <number>1</number>',
    description: 'IRP page header rich text label',
  },
  irpHeaderPlaceholder: {
    defaultMessage: 'Enter the page header here',
    description: 'IRP page header rich text placeholder',
  },
  irpContentLabel: {
    defaultMessage: 'Inbound Request Page Content <number>2</number>',
    description: 'IRP page content rich text label',
  },
  irpContentPlaceholder: {
    defaultMessage: 'Enter the page content here',
    description: 'IRP page content rich text placeholder',
  },
  irpFormHeaderLabel: {
    defaultMessage: 'Inbound Request Page Form Header <number>3</number>',
    description: 'IRP form header rich text label',
  },
  irpFormHeaderPlaceholder: {
    defaultMessage: 'Enter the form header here',
    description: 'IRP form header rich text placeholder',
  },
})

export const FormFields = () => {
  const { formatMessage } = useIntl()
  const { role, eventRole } = useMe().data.attributes
  const { systemName: eventName } = useEventLocation().location!.attributes
  const uploadImageUrl = `${window.location.protocol}//${window.location.host}/${eventName}/api/v1/upload-image`

  const {
    values: { enable_external_widget, enable_irp_instant_meeting },
    initialValues: {
      enable_external_widget: enableExternalWidget,
      enable_irp_instant_meeting: enableIrpInstantMeeting,
    },
  } = useFormState()

  return (
    <>
      <Group>
        <Toggle
          name="enable_external_widget"
          label={formatMessage(messages.enableIrpLabel)}
          desc={formatMessage(messages.enableIrpDesc)}
        />
      </Group>
      <Divider />
      {enable_external_widget === 'true' && (
        <>
          <Group>
            <IrpViewPageLink
              name="external_widget_token"
              label={formatMessage(messages.viewIrpPageLabel)}
              disabled={enableExternalWidget === 'false'}
            />
            <Toggle
              name="enable_company_logo_in_ext_widget"
              label={formatMessage(messages.companyLogoLabel)}
            />
            <Toggle
              name="enable_event_banner_in_ext_widget"
              label={formatMessage(messages.displayEventBannerLabel)}
            />
            {(eventRole ?? role) === 'CSM' && (
              <>
                <Toggle
                  name="enable_irp_instant_meeting"
                  label={formatMessage(messages.enableMeetNowLabel)}
                />
                {enable_irp_instant_meeting === 'true' && (
                  <>
                    <IrpViewPageLink
                      name="irp_instant_meeting_url"
                      label={formatMessage(messages.viewMeetNowPageLabel)}
                      disabled={enableIrpInstantMeeting === 'false'}
                    />
                    <IrpInput
                      name="irp_instant_meeting_label"
                      label={formatMessage(
                        messages.meetNowCallToActionInputLabel
                      )}
                      placeholder={formatMessage(
                        messages.meetNowCallToActionInputPlaceholder
                      )}
                    />
                  </>
                )}
              </>
            )}
            <IrpColorTheme />
            <IrpEngagementTypes />
          </Group>
          <IrpTextEditor>
            <EditorContainer>
              <IrpEditor
                name="external_widget_header_text"
                placeholder={formatMessage(messages.irpHeaderPlaceholder)}
                label={formatMessage(messages.irpHeaderLabel, {
                  number: (chunks: ReactNode) => (
                    <NumberCircle>{chunks}</NumberCircle>
                  ),
                })}
                config={{
                  toolbarGroups: [
                    { name: 'colors' },
                    { name: 'insert' },
                    { name: 'mode' },
                  ],
                  height: 80,
                  uploadImageUrl,
                  extraPlugins: 'uploadimage,sourcearea',
                }}
              />
              <IrpEditor
                name="external_widget_help_text"
                placeholder={formatMessage(messages.irpContentPlaceholder)}
                label={formatMessage(messages.irpContentLabel, {
                  number: (chunks: ReactNode) => (
                    <NumberCircle>{chunks}</NumberCircle>
                  ),
                })}
                config={{
                  toolbarGroups: [
                    { name: 'colors' },
                    { name: 'insert' },
                    { name: 'mode' },
                  ],
                  height: 150,
                  uploadImageUrl,
                  extraPlugins: 'uploadimage,sourcearea',
                }}
              />
              <IrpEditor
                name="external_widget_form_header_text"
                placeholder={formatMessage(messages.irpFormHeaderPlaceholder)}
                label={formatMessage(messages.irpFormHeaderLabel, {
                  number: (chunks: ReactNode) => (
                    <NumberCircle>{chunks}</NumberCircle>
                  ),
                })}
                config={{
                  toolbarGroups: [{ name: 'colors' }, { name: 'mode' }],
                  height: 80,
                  extraPlugins: 'sourcearea',
                }}
              />
            </EditorContainer>
            <PreviewContainer>
              <IrpPreview
                src={PreviewImage}
                alt={formatMessage(messages.altPreviewImage)}
              />
            </PreviewContainer>
          </IrpTextEditor>
          <CopySourceCodeContainer>
            <IrpEmbedSourceCodeCopy />
          </CopySourceCodeContainer>
          <CopySourceCodeContainer>
            <IrpCtaEmbedSourceCodeCopy />
          </CopySourceCodeContainer>
          {(eventRole ?? role) === 'CSM' &&
            enable_irp_instant_meeting === 'true' && (
              <>
                <CopySourceCodeContainer>
                  <MeetNowEmbedSourceCodeCopy />
                </CopySourceCodeContainer>
                <CopySourceCodeContainer>
                  <MeetNowCtaEmbedSourceCodeCopy />
                </CopySourceCodeContainer>
              </>
            )}
        </>
      )}
    </>
  )
}
