import { defineMessages, FormattedMessage, useIntl } from 'react-intl'
import { ALIGN, Radio, RadioGroup } from '@jifflenow/brightui/radio'
import { Field } from 'ui-components/Field'
import { getColorThemeRadioOverride } from './overrides'
import { ColorThemeLabel, Error, WidgetRoot } from './styled-components'
import { useIrpTheme } from './useIrpTheme'

const colors = defineMessages<string>({
  '#0096FF': {
    defaultMessage: 'Light blue',
    description: 'Color label',
  },
  '#2B2826': {
    defaultMessage: 'Dark gray',
    description: 'Color label',
  },
  '#535353': {
    defaultMessage: 'Gray',
    description: 'Color label',
  },
})

export const IrpColorTheme = () => {
  const { formatMessage } = useIntl()
  const theme = useIrpTheme()

  return (
    <Field<string>
      name="ext_widget_theme"
      format={(value: string) =>
        value
          ? JSON.stringify(JSON.parse(value).colors)
          : JSON.stringify(theme[Object.keys(theme)[0]])
      }
      parse={value => JSON.stringify({ colors: JSON.parse(value) })}
      render={({
        input: { value, onChange },
        meta: { error, submitError },
      }) => (
        <>
          {(submitError || error) && <Error>{submitError || error}</Error>}
          <WidgetRoot>
            <ColorThemeLabel>
              <FormattedMessage
                defaultMessage="Select the color theme for your page"
                description="Color theme label text"
              />
            </ColorThemeLabel>
            <RadioGroup
              name="colorTheme"
              value={value}
              onChange={e => onChange(e.target.value)}
              align={ALIGN.horizontal}
            >
              {Object.entries(theme).map(([name, value]) => (
                <Radio
                  key={name}
                  value={JSON.stringify(value)}
                  overrides={getColorThemeRadioOverride(
                    name,
                    formatMessage(colors[name])
                  )}
                />
              ))}
            </RadioGroup>
          </WidgetRoot>
        </>
      )}
    />
  )
}
