import { useEffect, useState } from 'react'
import {
  useCKEditor as useCKEditorBase,
  CKEditorEventAction,
} from 'ckeditor4-react'
import merge from 'deepmerge'
import { useStyletron } from '@jifflenow/brightui'

const editorUrl = `${process.env.REACT_APP_CKEDITOR_URL}?t=MAUD`

const wordCountDefaults = {
  showParagraphs: false,
  showWordCount: false,
  showCharCount: true,
  countSpacesAsChars: true,
  hardLimit: true,
}

const config = {
  toolbarGroups: [
    { name: 'basicstyles', groups: ['basicstyles'] },
    { name: 'styles', groups: ['styles'] },
    { name: 'links', groups: ['links'] },
    { name: 'clipboard', groups: ['undo'] },
    { name: 'forms', groups: ['list'] },
  ],
  extraPlugins: 'editorplaceholder, wordcount',
  removeButtons:
    'Subscript,Superscript,Anchor,PasteFromWord,PasteText,Paste,Copy,Cut,Styles,Table',
  format_tags: 'p;h1;h2;h3;pre',
  removeDialogTabs: 'image:advanced;link:advanced',
  linkShowTargetTab: false,
  linkShowAdvancedTab: false,
  dialog_backgroundCoverColor: 'black',
  skin: 'bright',
  delayIfDetached: true,
}

const showTopBar = (editorId, isShowTopBar) => {
  const editorTop = document.getElementById(editorId + '_top')
  if (editorTop) {
    editorTop.style.display = isShowTopBar ? 'block' : 'none'
  }
}

export const useCKEditor = props => {
  const {
    placeholder,
    input: { value, onChange, onFocus, onBlur },
    meta,
    showTopBarOnFocus: isShowTopBarOnFocus,
    config: customConfig,
    maxLength,
  } = props

  const [, { colors }] = useStyletron()

  const [element, setElement] = useState()
  const [showLoader, setShowLoader] = useState(true)

  const {
    editor,
    status: editorStatus,
    error: ckeditorError,
  } = useCKEditorBase({
    editorUrl,
    config: {
      ...merge(config, customConfig),
      editorplaceholder: placeholder,
      wordcount: {
        ...wordCountDefaults,
        ...(maxLength ? { maxCharCount: maxLength } : null),
      },
    },
    debug: process.env.NODE_ENV === 'development',
    initContent: value,
    dispatchEvent: ({ type, payload }) => {
      const editorId = payload.editor.id
      if (type === CKEditorEventAction.change) {
        onChange(payload.editor.getData())
      }
      if (type === CKEditorEventAction.focus) {
        onFocus()
        isShowTopBarOnFocus && showTopBar(editorId, true)
      }
      if (type === CKEditorEventAction.blur) {
        onBlur()
        isShowTopBarOnFocus && showTopBar(editorId, false)
      }
    },
    element,
    subscribeTo: ['change', 'focus', 'blur'],
  })

  useEffect(() => {
    const style = {
      'border-color':
        meta.touched && !!meta.error
          ? colors.negative
          : meta.active
          ? colors.primary
          : colors.mono600,
      'border-style': 'solid',
      'border-width': '1px',
      'border-radius': '4px',
      overflow: 'hidden',
    }
    const canSetStyles =
      editor && (editorStatus === 'loaded' || editorStatus === 'ready')

    if (canSetStyles) {
      editor.container.setStyles(style)
    }

    return () => {
      if (canSetStyles) {
        Object.keys(style).forEach(styleName => {
          editor.container.removeStyle(styleName)
        })
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [editor, editorStatus, meta])

  useEffect(() => {
    if (editorStatus === 'ready' || editorStatus === 'loaded') {
      setShowLoader(false)
      editor.setData(value)
      if (isShowTopBarOnFocus) {
        showTopBar(editor.id, false)
      }

      const editorContainer = editor.container.$
      editorContainer
        .querySelector('.cke_bottom')
        .setAttribute(
          'style',
          'border-top: none !important; background: none !important; padding: 0px 8px 2px 0px !important;'
        )
      editorContainer.querySelector(
        '.cke_bottom .cke_wordcount'
      ).style.display = 'none'

      const ckeResizeElement = editorContainer.querySelector(
        '.cke_bottom .cke_resizer'
      )
      if (ckeResizeElement) {
        ckeResizeElement.setAttribute('style', 'margin-top: 0')
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [editorStatus])

  const editorStyle =
    editorStatus !== 'ready' && editorStatus !== 'loaded'
      ? { visibility: 'hidden' }
      : undefined

  return {
    editor,
    showLoader,
    setElement,
    editorStyle,
    ckeditorError,
  }
}
