import { FC, VFC } from 'react'
import merge from 'deepmerge'
import { FieldRenderProps } from 'react-final-form'
import { useCKEditor } from '@jifflenow/ckeditor'
import { Field } from 'ui-components/Field'
import {
  EditorLabel,
  EditorLabelContainer,
  EditorRoot,
  Error,
} from './styled-components'
import { IrpEditorProps } from './types'

const defaultConfig = {
  toolbarGroups: [{ name: 'paragraph', groups: ['align'] }],
}

const RichText: FC<FieldRenderProps<string>> = ({
  input,
  meta,
  config,
  placeholder,
}) => {
  const { setElement, editorStyle } = useCKEditor({
    placeholder,
    input,
    meta,
    config: merge(defaultConfig, config),
    showTopBarOnFocus: true,
  })

  return <div id={input.name} ref={setElement} style={editorStyle} />
}

export const IrpEditor: VFC<IrpEditorProps> = ({
  name,
  label,
  placeholder,
  config,
}) => (
  <Field<string>
    name={name}
    parse={value => value ?? ''}
    render={props => {
      const {
        meta: { submitError, error },
      } = props

      return (
        <>
          {(submitError || error) && <Error>{submitError || error}</Error>}
          <EditorRoot>
            <EditorLabelContainer>
              <EditorLabel htmlFor={name}>{label}</EditorLabel>
            </EditorLabelContainer>
            <RichText {...props} placeholder={placeholder} config={config} />
          </EditorRoot>
        </>
      )
    }}
  />
)
